* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}
#overallContainer {
  position: fixed;
  display: flex;
  align-items: center;
  min-width: 100vw;
  flex-direction: column;
  min-height: 100vh;
  background-size: 100% 100%;
}
#inputContainer {
  margin-top: 20px;
  color: azure;
  width: 400px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: transparent;
  border-radius: 10px;
  background: linear-gradient(rgba(0,0,0,0.6),rgba(0,0,0,0.6));
}
#input {
  background: transparent;
  border: transparent;
  border-bottom: 2px solid white;
  width:60%;
  height: 50%;
  color: #ecf0f1;
  font-size: 1.4rem;
  text-align: center;
}
#input:focus {
  outline: none;
}
#input::placeholder {
  font-size: 1.2rem;
  text-align: center;
  color: #ecf0f1;
}
#cardContainer {
  background: linear-gradient(rgba(0,0,0,0.6),rgba(0,0,0,0.6));
  background-color: transparent;
  color: azure;
  min-width: 400px;
  min-height: 77vh;
  margin-top: 20px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-evenly;
}
#icon {
  background-color: #95a5a6;
  border-radius: 50%;
  height: 90px;
  width: 90px;
}
#location {
  font-size: 2.5rem;
  margin-right: 8px;
  color: #e67e22;
}
#city {
  font-size: 2.8rem;
  font-weight: 600;
  padding: 0 15px;
}
#temp {
  font-size: 5.2rem;
  font-weight: 400;
  color: #e67e22;
}
#date {
  letter-spacing: 0.2rem;
  font-size: 1rem;
  margin-top: -20px;
}
#min_max {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  font-weight: 400;
  font-size: 1.3rem;
}
#myVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
}
